/**
 * Renderer
 * --------
 * Componente responsable exclusivamente de renderizar el HTML
 * dentro del iframe.
 *
 * - No gestiona eventos
 * - No mide interaccin
 * - No contiene lgica experimental
 *
 * La separacin renderer / logger es intencional para mantener
 * una arquitectura clara y facilitar el anlisis posterior.
 */

import React from "react";

// Props necesarias para renderizar el estmulo y exponer su nodo DOM al logger
interface RendererProps {
  html: string;
  containerRef: React.RefObject<HTMLDivElement>;
}

// Renderiza el HTML del estmulo y asigna la referencia al contenedor DOM
export const Renderer = ({ html, containerRef }: RendererProps) => {
  return (
    <div className="cs-root" ref={containerRef}>
      <div
        className="cs-content"
        dangerouslySetInnerHTML={{ __html: html }}
      />
    </div>
  );
};


