/**
 * Event Logger Component
 *
 * Low-level interaction event logger.
 * Logs observable interaction primitives without interpretation.
 */


/**
 * Punto de entrada del componente EventLogger.
 *
 * Coordina el ciclo completo del componente:
 * - renderizado del estmulo HTML
 * - captura e instrumentacin de eventos de interaccin
 * - envo de eventos al backend de Streamlit
 *
 * Acta como frontera explcita entre:
 * frontend (medicin conductual y timestamps)
 * backend (recogida y anlisis de datos)
 */
import React, { useEffect, useRef } from "react";
import ReactDOM from "react-dom/client";
import { Streamlit, withStreamlitConnection } from "streamlit-component-lib";

import { Renderer } from "./HtmlRenderer";
import { attachEventLogger } from "../../../core/eventLogger";

// Define los argumentos de entrada enviados desde Python (Streamlit)
interface Props {
  args: {
    html?: string;
  };
}

// Componente principal que renderiza el estmulo y gestiona la captura de eventos
const App = ({ args }: Props) => {
  const { html = "" } = args;
  const containerRef = useRef<HTMLDivElement>(null);

  // Inicializa la captura de interacciones dentro del contenedor y enva cada evento registrado a Streamlit
  useEffect(() => {
    if (!containerRef.current) return;

    const detach = attachEventLogger(
      containerRef.current,
      (event) => {
        Streamlit.setComponentValue(event);
        

      }
    );

    return detach;
  }, [html]);

  // Bug de la altura y botones. Calcula altura total
  const updateHeight = () => {
    const height = document.body.scrollHeight;
    Streamlit.setFrameHeight(height);
  };

  // Seala a Streamlit que el componente est listo y ajusta el alto del iframe
  useEffect(() => {
 
    Streamlit.setComponentReady();
   
    //Streamlit.setFrameHeight();
    
    // *****************************
    // Bug de la altura
    
    // Primer ajuste (post-render inmediato)
    updateHeight();

     // Ajustes diferidos para imgenes / layout async
    const t1 = setTimeout(updateHeight, 100);
    const t2 = setTimeout(updateHeight, 300);
    const t3 = setTimeout(updateHeight, 600);

    return () => {
      clearTimeout(t1);
      clearTimeout(t2);
      clearTimeout(t3);
    };
    // Fin bug de la altura
    // ********************************

  }, [html]);



  // Envia tiempo inicial (t0) al cliente
  useEffect(() => {
    const t0 = Date.now();

    Streamlit.setComponentValue({
      type: "render",
      ts: t0,
      target: null,
      duration: null,
    });
  }, [html]);

  return <Renderer html={html} containerRef={containerRef} />;
};

// Conecta el componente al ciclo de vida de Streamlit
const Connected = withStreamlitConnection(App);

const rootElement = document.getElementById("root");
if (rootElement) {
  const root = ReactDOM.createRoot(rootElement);
  root.render(<Connected />);
}
