/**
 * EventLogger  Core
 *
 * Logger de bajo nivel para eventos de interaccin.
 *
 * Captura eventos atmicos y observables de interaccin sobre elementos HTML
 * instrumentados mediante el atributo `data-track`.
 *
 * Este mdulo es:
 * - agnstico de framework (no React, no Streamlit)
 * - agnstico de dominio (no phishing, no UX, no negocio)
 * - libre de interpretacin (no heursticos, no agregaciones)
 *
 * Emite eventos raw con timestamps locales.
 * Cualquier interpretacin de mayor nivel debe realizarse aguas abajo.
 */

/**
 * Estructura base comn a todos los eventos de interaccin.
 */
export type BaseInteractionEvent = {
  /** Tipo de evento: hover, link..*/
  type: string;

  /** Timestamp local en milisegundos (Date.now) */
  ts: number;

  /** Identificador semntico del elemento (data-track) */
  target: string | null;
};

/**
 * Evento de hover.
 *
 * Se emite cuando el puntero abandona un elemento instrumentado.
 * La duracin siempre est presente y representa el tiempo de hover en ms.
 */
export type HoverEndEvent = BaseInteractionEvent & {
  type: "hover_end";
  duration: number;
};

/**
 * Evento de click.
 *
 * Se emite de forma inmediata al producirse el click.
 * No incluye duracin.
 */
export type ClickEvent = BaseInteractionEvent & {
  type: "click";
};

/**
 * Unin de todos los eventos que puede emitir el core del EventLogger.
 */
export type EventLoggerEvent = HoverEndEvent | ClickEvent;

/**
 * Registra listeners de interaccin sobre un subrbol del DOM
 * y emite eventos crudos de interaccin mediante el callback proporcionado.
 *
 * @param root  Elemento raz que contiene los elementos instrumentados
 * @param emit  Funcin callback invocada para cada evento capturado
 *
 * @returns Funcin de limpieza para desregistrar los listeners
 */
export function attachEventLogger(
  root: HTMLElement,
  emit: (e: EventLoggerEvent) => void
) {
  // Mapa por elemento para registrar el inicio del hover
  // Evita solapamientos y eventos duplicados
  const hoverStartMap = new Map<HTMLElement, number>();

  // Seleccin explcita de elementos instrumentados
  const elements = root.querySelectorAll<HTMLElement>("[data-track]");

  const onEnter = (e: Event) => {
    const el = e.currentTarget as HTMLElement;

    // Evita mltiples registros de entrada en el mismo elemento
    if (hoverStartMap.has(el)) return;

    hoverStartMap.set(el, Date.now());
  };

  const onLeave = (e: Event) => {
    const el = e.currentTarget as HTMLElement;
    const target = el.getAttribute("data-track");

    const start = hoverStartMap.get(el);
    if (!start) return;

    const now = Date.now();
    hoverStartMap.delete(el);

    emit({
      type: "hover_end",
      target,
      duration: now - start,
      ts: now,
    });
  };

  const onClick = (e: Event) => {
    const el = e.currentTarget as HTMLElement;
    const target = el.getAttribute("data-track");

    emit({
      type: "click",
      target,
      ts: Date.now(),
    });
  };

  elements.forEach((el) => {
    el.addEventListener("mouseenter", onEnter);
    el.addEventListener("mouseleave", onLeave);
    el.addEventListener("click", onClick);
  });

  // Funcin de cleanup: elimina todos los listeners registrados
  return () => {
    elements.forEach((el) => {
      el.removeEventListener("mouseenter", onEnter);
      el.removeEventListener("mouseleave", onLeave);
      el.removeEventListener("click", onClick);
    });
  };
}
